.app-header {
    background-color: #1a1f36;
    color: #e0e0e0;
    padding: 1rem 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    flex-wrap: wrap;
}

.header-content {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
}

.header-left h1 {
    margin: 0;
    font-size: 1.5rem;
    color: #f5f5f5;
}

.header-left .header-date {
    margin: 0;
    font-size: 0.9rem;
    color: #a0a0a0;
}

.header-right {
    display: flex;
    align-items: center;
    gap: 1rem;
    flex-wrap: wrap;
}

.user-avatar {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #4a90e2;
    color: white;
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
    position: relative;
}

/* Custom Tooltip */
.user-avatar:hover::after {
    content: attr(title);
    position: absolute;
    top: 50px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #4a90e2;
    color: white;
    padding: 0.5rem;
    border-radius: 5px;
    font-size: 0.9rem;
    white-space: nowrap;
    opacity: 1;
    transition: opacity 0.3s ease;
}

.logout-button {
    background-color: #4a90e2;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    font-size: 0.9rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.logout-button:hover {
    background-color: #5c9ee5;
}

@media (max-width: 768px) {
    .app-header {
        flex-direction: column;
        align-items: flex-start;
        padding: 1rem;
    }

    .header-content {
        flex-direction: column;
        align-items: flex-start;
    }

    .header-left h1 {
        font-size: 1.2rem;
    }

    .header-left .header-date {
        font-size: 0.8rem;
    }

    .header-right {
        flex-direction: column;
        align-items: flex-start;
        gap: 0.5rem;
    }

    .logout-button {
        width: 100%;
    }

    /* Hide user-avatar on mobile */
    .user-avatar {
        display: none;
    }
}